import React from "react";

export default function Translation({ doStuff, setInput, result,History }) {
  
  return (
    <div>
      <textarea
        className="textarea"
        cols={55}
        rows={10} 
        onChange={(e) => setInput(e.target.value)}
      ></textarea>
      <button className="button1" onClick={doStuff}>
        Check it
      </button>
      

      <h3 className="result-text">{result.length > 0 ? result : ""}</h3>
    </div>
  );
}